import react, {Component} from 'react';
import 'antd/dist/antd.css';
import Post from './Post';
import { Row } from 'antd';
import {
  Input,
  Button,
} from 'antd';
import Banner from './Banner';
import { Carousel } from 'antd';
import './isi.css';
import {SearchOutlined} from '@ant-design/icons';

class Isi extends Component {
  state ={
    data : [],
    value:'',
    banner: []
  }

  getValue = (event) => {
    this.setState({value: event.target.value})
  
  }
  handleSubmit = () => {
    const value = this.state.value;
     fetch('https://www.omdbapi.com/?apikey=dfc8a048&s=' + value)
     .then(response=> response.json())
    .then(response =>{
      this.setState({
        data: response.Search
      })

    })
  }
  componentDidMount(){ 
    fetch('https://www.omdbapi.com/?apikey=dfc8a048&s=dragon')
    .then(response=> response.json())
   .then(response =>{
     this.setState({
       banner: response.Search,
    
     })
  })
}

  render(){
  return (
    <div>
       <Input.Group style={{paddingBottom: 5}} compact>
      <Input style={{ width: 'calc(40% - 100px)', marginLeft:'700px', borderTopLeftRadius:'5px',borderBottomLeftRadius:'5px'}} onChange= {this.getValue} placeholder="Search movie..." />
      <Button style={{borderTopRightRadius:'5px', borderBottomRightRadius:'5px'}} type="primary" onClick={this.handleSubmit}><SearchOutlined style={{fontSize:21}} /></Button>
    </Input.Group>
       <Carousel className="banner" autoplay>
         {
          this.state.banner.map(banner =>{
            return  <Banner  key={banner.imdbID} judul = {banner.Title} gambar = {banner.Poster} tipe = {banner.Type} tahun = {banner.Year}/>
      })
    }
    </Carousel>
      <div className="content">
    <div className="site-card-border-less-wrapper">
    <Row gutter={16}>{
       this.state.data.map(post =>{
        return  <Post  key={post.imdbID} title = {post.Title} Years = {post.Year} Type = {post.Type} Poster = {post.Poster}/>
      })
    }
     </Row>
      </div>
      </div>
      </div>
  )
}
}
export default Isi;
