import React from 'react';
import 'antd/dist/antd.css';
import { Descriptions } from 'antd';
import './banner.css';
import { notification } from 'antd';
import {PlayCircleFilled} from '@ant-design/icons';
import {BookOutlined} from '@ant-design/icons';
import { Rate } from 'antd';


const contentStyle = {
    height: '500px',
    width:'500px',
    marginLeft:'5px',
    padding: '70px',
    color: '#fff',
    lineHeight: '160px',
    textAlign: 'center',
  };

  const openNotification = () => {
    notification.open({
      message: 'Notification Title',
      description:
        'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
      onClick: () => {
        console.log('Notification Clicked!');
      },
    });
  };

  const Banner = (props) => {
  return (
      <div >
        <img style={contentStyle} src={props.gambar}/>
        <Descriptions className="deskripsi" title={props.judul}>
    <Descriptions.Item label="Tipe">{props.tipe}</Descriptions.Item>
    <Descriptions.Item label="Tahun">{props.tahun}</Descriptions.Item>
    <Descriptions.Item label="Director">Hangzhou, Zhejiang</Descriptions.Item>
    <Descriptions.Item label="Penulis">empty</Descriptions.Item>
    <Descriptions.Item label="Actors">
      No. 18, Wantang Road, Xihu District, Hangzhou, Zhejiang, China
    </Descriptions.Item>
    <Descriptions.Item></Descriptions.Item>
    <Descriptions.Item label="Rate"><Rate style={{marginTop:-5 ,marginLeft:5}} disabled defaultValue={4} /></Descriptions.Item>
    <Descriptions.Item >
    <BookOutlined className="btn" onClick={openNotification} style={{ fontSize:50, marginLeft:250}}/>
    <PlayCircleFilled className="btn" onClick={openNotification}  style={{ fontSize:50, marginLeft:10}}/>
   
    </Descriptions.Item>
      </Descriptions>
    </div>
  )
}

export default Banner;