import React from "react";
import 'antd/dist/antd.css';
import './Post.css'
import { Card, Col } from 'antd';
import { Rate } from 'antd';
import { notification } from 'antd';
import {PlayCircleFilled} from '@ant-design/icons';
import {BookOutlined} from '@ant-design/icons';

const openNotification = () => {
  notification.open({
    message: 'Notification Title',
    description:
      'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
    onClick: () => {
      console.log('Notification Clicked!');
    },
  });
};

const Post = (props) => {
    return(
          <Col style={{padding:15}} span={8}>
            <Card className="card" title={props.title} style={{ marginBottom: 30 }} bordered={false}>
              <img style={{ marginBottom: 10 }} className="poster" src={props.Poster}></img>
              <h3 style={{ marginBottom: 10 }}>Type : {props.Type}</h3>
              <p style={{ marginBottom: 10 }}>Year : {props.Years}</p>
              <p style={{ marginBottom: 10 }}><Rate style={{fontSize:16,}}/></p>
               <PlayCircleFilled className="btn" onClick={openNotification}  style={{ fontSize:50, marginLeft:10}}/>
              <BookOutlined className="btn" onClick={openNotification} style={{ fontSize:50}}/>
             
              
            </Card>
           
          </Col>
    )
}
export default Post;